import Vue from "vue";
import axios from "axios";
import store from './store'

     // ;config={header,params,data,method}
    axios.interceptors.request.use(function(config){
        store.commit('VIEW_LOAD',true)
        // console.log(' go',vm.$data.bLoad)
        return config;
    },function(error){
        return Promise.reject(error);
    });
    // 
    axios.interceptors.response.use(function(response){
        store.commit('VIEW_LOAD',false)
        // console.log(' back')
        return response;
},function(error){
        return Promise.reject(error);
})
 

Vue.prototype.$axios=axios;
window.axios = axios;

export default axios;